@charset "UTF-8";
/*? ======= ПЕРЕМЕННЫЕ ПРОЕКТА НАЧАЛО ======= */
:root {
  --font-family: "Inter", sans-serif;
  --content-width: 1215px;
  --container-offset: 10px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-white: #fff;
  --color-black: #1d252b;
  --color-grin: #386641;
  --color-light-grin: #6a994e;
  --color-red: #bc4749;
  --color-gradient: linear-gradient(180deg, rgba(52, 93, 60, 1), rgba(33, 45, 46, 1));
  --color-shadow-grin: rgba(0, 0, 0, 0.25);
  --color-shadow-black: rgba(0, 0, 0, 0.15);
  --radius7: 7px;
  --radius12: 12px;
  --box-shadow: 0px 3.8px 94.7px 0px;
}

/*? ======= ПЕРЕМЕННЫЕ ПРОЕКТА КОНЕЦ ======= */
