/*? ======= ПОДКЛЮЧЕНИЕ ШРИФТОВ ПРОЕКТА НАЧАЛО ======= */
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/InterRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/InterMedium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/InterBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/InterExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-display: swap;
  font-style: normal;
}
/*? ======= ПОДКЛЮЧЕНИЕ ШРИФТОВ ПРОЕКТА НАЧАЛО ======= */
/*? ======= ОСНОВНЫЕ НАСТРОЙКИ ПРОЕКТА НАЧАЛО ======= */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  background-color: var(--color-white);
}

.page__body {
  padding-top: 35px;
  margin: 0 auto;
  min-width: 320px;
  max-width: 1306px;
  min-height: 100%;
  height: 891px;
  font-size: 16px;
  color: var(--color-white);
  border-radius: var(--radius12);
  background: var(--color-gradient);
  padding-bottom: 10px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

/*? ======= ОСНОВНЫЕ НАСТРОЙКИ ПРОЕКТА КОНЕЦ ======= */
/*? ======= HEADER НАЧАЛО ======= */
.header {
  width: 100%;
  background-color: transparent;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 30px;
}
.header__logo-image {
  margin-left: 4px;
}
.header__logo-text {
  font-size: 11.446px;
  font-style: normal;
  font-weight: 700;
  line-height: 128.023%; /* 14.653px */
  text-decoration-line: line-through;
}
.header__nav {
  margin: 0;
  list-style: none;
  display: flex;
  gap: 48px;
  margin-left: -393px;
}
.header__nav-link {
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 128.023%; /* 15.479px */
  color: var(--color-white);
  opacity: 0.8;
  transition: 0.3s;
}
.header__nav-link:hover {
  opacity: 1;
  transition: 0.3s;
}
.header__login {
  display: flex;
  gap: 14px;
  font-size: 12px;
  font-style: normal;
  line-height: 128.023%; /* 15.363px */
}
.header__btn-login {
  font-weight: 400;
  color: var(--color-white);
  opacity: 0.8;
  transition: 0.3s;
}
.header__btn-login:hover {
  opacity: 1;
  transition: 0.3s;
}
.header__btn-signup {
  width: clamp(5rem, 4.05rem + 4.75vw, 7.125rem);
  height: clamp(1.875rem, 1.624rem + 1.26vw, 2.438rem);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--color-red);
  border: 1px solid #d66567;
  border-radius: var(--radius7);
  font-weight: 500;
  color: var(--color-white);
  transition: border, background-color, 1s ease;
}
.header__btn-signup:hover {
  border: 1px solid var(--color-white);
  background-color: #962628;
}
.header__burger {
  display: none;
}

/*? ======= HEADER КОНЕЦ ======= */
/*? ======= HEADER MEDIA НАЧАЛО ======= */
@media screen and (max-width: 1100px) {
  .header__nav {
    margin-left: 0;
    padding: 0;
  }
}
@media screen and (max-width: 761px) {
  .header__nav {
    width: 0;
    height: 0;
    scale: 0;
    padding-top: 20px;
    padding-bottom: 40px;
    flex-direction: column;
    position: absolute;
    top: 70px;
    left: 50%;
    z-index: 3;
    transform: translateX(-50%);
    background-color: transparent;
    transition: 1s;
    border-radius: var(--radius12);
    background: var(--color-gradient);
  }
  .header__nav-link {
    font-size: 1.5em;
    text-align: center;
    display: flow;
  }
  .header__burger {
    height: 20px;
    width: 30px;
    padding: 0;
    border: none;
    background-color: transparent;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }
  .header__burger span {
    width: 100%;
    height: 2px;
    background-color: var(--color-white);
    transition: 0.5s;
  }
  .js-menu--active {
    display: flex;
    width: 320px;
    height: auto;
    scale: 1;
    transition: 1s;
  }
  .js-burger--active {
    justify-content: center;
  }
  .js-burger--active span:nth-child(2) {
    display: none;
  }
  .js-burger--active span:nth-child(1) {
    position: absolute;
    transform: rotate(45deg);
  }
  .js-burger--active span:nth-child(3) {
    position: absolute;
    transform: rotate(-45deg);
  }
}
/*? ======= HEADER MEDIA КОНЕЦ ======= */
/*? ======= HERO НАЧАЛО ======= */
.hero {
  position: relative;
  /*? ======= Анимация начало ======= */
  /*? ======= Анимация конец ======= */
}
.hero__title {
  margin-top: clamp(1.25rem, 0.496rem + 3.77vw, 2.938rem);
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 0;
  text-align: center;
  font-size: clamp(1.75rem, 1.359rem + 1.96vw, 2.625rem);
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  color: var(--color-white);
}
.hero__card-block {
  width: 970px;
  margin: 69px auto;
  padding-left: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.hero__card {
  width: 334px;
  min-width: 300px;
  height: 592px;
  transform: translateY(1000px);
  transition: 1s;
  padding-top: 38px;
  padding-bottom: 53px;
  background-color: var(--color-light-grin);
  border-radius: var(--radius12);
}
.hero__card--right {
  margin-right: -12px;
}
.hero__card-header, .hero__card-body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding-left: 60px;
  padding-right: 60px;
}
.hero__card-body {
  height: 53.5%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.hero__card-title {
  font-weight: 600;
  line-height: normal;
  font-style: normal;
  color: var(--color-white);
}
.hero__card-price-wrap {
  position: relative;
  padding-bottom: 7px;
  margin-top: 15px;
}
.hero__card-price {
  padding-bottom: 7px;
  margin-left: 10px;
  margin-top: 15px;
  font-size: 38px;
  font-style: normal;
  font-weight: 800;
  line-height: 128.023%; /* 48.454px */
}
.hero__card-currency {
  font-size: 12px;
  position: absolute;
  left: 0;
  top: 0;
  cursor: pointer;
}
.hero__card-period {
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  color: var(--color-white);
  opacity: 0.7;
  transition: 0.3s;
}
.hero__card-period:hover {
  cursor: pointer;
  opacity: 1;
  transition: 0.3s;
}
.hero__card-text {
  padding-top: 15px;
  padding-bottom: 27px;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%; /* 16.8px */
  color: var(--color-white);
  opacity: 0.8;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.hero__card-features {
  font-size: 15.139px;
  font-style: normal;
  font-weight: 600;
  line-height: 128.023%; /* 19.382px */
  padding-left: 60px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 22px;
  margin-bottom: 8px;
}
.hero__card-tariff {
  display: block;
  font-size: 13.247px;
  font-style: normal;
  font-weight: 500;
  line-height: 128.023%; /* 16.959px */
  padding-left: 60px;
  margin-bottom: 24px;
}
.hero__card-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.hero__card-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 5px;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 128.023%; /* 15.363px */
  color: var(--color-white);
  opacity: 0.8;
}
.hero__card-item:not(:last-child) {
  margin-bottom: 15px;
}
.hero__card--right .hero__card-item:not(:last-child) {
  margin-bottom: 17px;
}
.hero__card-item--500 {
  font-weight: 500;
  opacity: 1;
}
.hero__card-row {
  width: 10px;
  height: 15px;
}
.hero__card-row use {
  fill: var(--color-red);
}
.hero__card-btn {
  width: 214px;
  height: 39px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 128.023%; /* 15.363px */
  color: var(--color-white);
  border-radius: var(--radius7);
  border: 1px solid #d0d0d0;
  background: transparent;
  box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.1);
  transition: 0.5s;
}
.hero__card-btn:hover {
  background: #3e5a46;
  transition: 0.5s;
}
.hero__card--black {
  height: 599px;
  padding-top: 44px;
  background-color: var(--color-black);
  position: absolute;
  top: 1000px;
  left: 51.3%;
  transform: translate(-50%);
}
.hero__card--black .hero__card-features {
  padding-top: 18px;
  border-top: none;
}
.hero__card--black .hero__card-item:not(:last-child) {
  margin-bottom: 17px;
}
.hero__card--black .hero__card-body {
  height: 52.3%;
}
.hero__card-btn--red {
  border: 1px solid #d66567;
  background-color: var(--color-red);
  gap: 10px;
  transition: 0.5s;
}
.hero__card-btn--red:hover {
  transition: 0.5s;
  background-color: #962628;
}
.hero__card-icon-btn {
  width: 23px;
  height: 23px;
}
.hero__card-icon-btn use {
  stroke: var(--color-red);
  fill: #fff;
}
.hero__title-image {
  position: absolute;
  top: -92px;
  left: 0;
  object-fit: cover;
}
.hero__card.hero__card-animation {
  transform: translateY(0);
}
.hero__card.hero__card-animation:nth-child(1) {
  transition-delay: 1s;
}
.hero__card.hero__card-animation:nth-child(2) {
  transition-delay: 3s;
}
.hero__card.hero__card-animation:nth-child(3) {
  transition-delay: 2s;
}
.hero__card--black.hero__card-animation {
  top: -37px;
  left: 51.3%;
  transform: translate(-50%);
  z-index: 2;
}

/*? ======= HERO КОНЕЦ ======= */
/*? ======= HERO MEDIA НАЧАЛО ======= */
@media screen and (max-width: 1300px) {
  .hero__title-image {
    display: none;
  }
}
@media screen and (max-width: 1126px) {
  .hero__card-block {
    padding-left: 0;
  }
  .hero__card--black {
    left: 50%;
  }
  .hero__card--right {
    margin-right: 0;
  }
  .hero__card--black.hero__card-animation {
    top: -37px;
    left: 50%;
    transform: translate(-50%);
    z-index: 2;
  }
}
@media screen and (max-width: 987px) {
  .page__body {
    height: auto;
  }
  .hero {
    height: auto;
  }
  .hero__card-block {
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
  }
  .hero__card-block {
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
  }
  .hero__card-btn {
    width: 100%;
  }
  .hero__card--black {
    position: static;
    transform: none;
    transform: translateY(1000px);
    transition: 1s;
  }
  .hero__card--black.hero__card-animation {
    position: static;
    transform: none;
    transition-delay: 1s;
  }
  .hero__card.hero__card-animation:nth-child(2) {
    transition-delay: 2s;
  }
  .hero__card.hero__card-animation:nth-child(3) {
    transition-delay: 3s;
  }
}
@media screen and (max-width: 718px) {
  .hero__card {
    transform: translateY(0px);
  }
}
/*? ======= HERO MEDIA КОНЕЦ ======= */
p {
  background-color: blue;
  font-size: 20px;
}